import React from 'react';
import Highcharts from 'highcharts/highstock';
import HighchartsReact from 'highcharts-react-official';


import OPTIONS_ONE from './options/optionsOne';
import OPTIONS_TWO from './options/optionsTwo';
import OPTIONS_THREE from './options/optionsThree';

// import callAddFont from './font/loadFont';
import { initUCOEURData, initUCOUSDData, initUCOBTCData, initUCOETHData, initBTCUSDData, initETHUSDData } from './data/initData';


import './App.css';

require('highcharts/modules/exporting')(Highcharts)
require('highcharts/modules/offline-exporting')(Highcharts)
require('highcharts/themes/dark-unica')(Highcharts)

Highcharts.setOptions({
  lang: {
    thousandsSep: ','
  }
});

class App extends React.Component {
  constructor() {
    super();

    this.state = {
      optionsOne: OPTIONS_ONE,
      optionsTwo: OPTIONS_TWO,
      optionsThree: OPTIONS_THREE
    }
  }

  componentDidMount() {
    const secondsSinceEpoch = Math.round(Date.now() / 1000);

    Promise.all([
      fetch('https://api.coingecko.com/api/v3/coins/uniris/market_chart?vs_currency=usd&days=max').then(res => res.json()),
      fetch('https://api.coingecko.com/api/v3/coins/uniris/market_chart?vs_currency=eur&days=max').then(res => res.json()),
      fetch('https://api.coingecko.com/api/v3/coins/uniris/market_chart?vs_currency=btc&days=max').then(res => res.json()),
      fetch('https://api.coingecko.com/api/v3/coins/uniris/market_chart?vs_currency=eth&days=max').then(res => res.json()),
      fetch(`https://api.coingecko.com/api/v3/coins/bitcoin/market_chart/range?vs_currency=usd&from=1599091200&to=${secondsSinceEpoch}`).then(res => res.json()),
      fetch(`https://api.coingecko.com/api/v3/coins/ethereum/market_chart/range?vs_currency=usd&from=1599091200&to=${secondsSinceEpoch}`).then(res => res.json())
    ]).then(res => {
      res[0].prices = [...initUCOUSDData.prices, ...res[0].prices];
      res[0].total_volumes = [...initUCOUSDData.total_volumes, ...res[0].total_volumes];
      res[1].prices = [...initUCOEURData.prices, ...res[1].prices];
      res[2].prices = [...initUCOBTCData.prices, ...res[2].prices];
      res[3].prices = [...initUCOETHData.prices, ...res[3].prices];
      res[4].prices = [...initBTCUSDData.prices, ...res[4].prices];
      res[5].prices = [...initETHUSDData.prices, ...res[5].prices];
      this.setState({
        optionsOne: {
          series: [
            { data: res[0].prices },
            { data: res[1].prices },
            { data: res[2].prices },
            { data: res[3].prices }
          ]
        },
        optionsTwo: {
          series: [
            { data: res[0].prices },
            { data: res[4].prices },
            { data: res[5].prices }
          ]
        },
        optionsThree: {
          series: [
            { data: res[0].prices },
            { data: res[0].total_volumes }
          ]
        }
      })
    });

    var self = this;

    fetch('https://srv-store1.gofile.io/download/1NtARO/NotoSans-Regular.ttf')
      .then(res => res.blob())
      .then(function (res) {
        let reader = new FileReader();
        reader.readAsDataURL(res);
        reader.onloadend = function () {
          var base64String = reader.result;
          base64String = base64String.substr(base64String.indexOf(',') + 1);
          self.loadHighchartsLib(base64String);
          // console.log(base64String);
        }
      });


    // this.loadHighchartsLib();
  }

  loadHighchartsLib(base64String) {

    const addFontFile = function () {
      this.addFileToVFS('NotoSans-Regular-normal.ttf', base64String);
      this.addFont('NotoSans-Regular-normal.ttf', 'NotoSans-Regular', 'normal');
      // console.log(base64String);
      // console.log(this);
    }

    const script1 = document.createElement("script");
    script1.src = "https://code.highcharts.com/9.0.0/lib/jspdf.js";
    script1.async = true;
    script1.onload = () => {
      window.jsPDF.API.events.push(['addFonts', addFontFile]);
    };
    document.body.appendChild(script1);

    const script2 = document.createElement("script");
    script2.src = "https://code.highcharts.com/9.0.0/lib/svg2pdf.js";
    script2.async = true;
    document.body.appendChild(script2);
  }

  render() {
    return (
      <div className='parent'>

        <HighchartsReact
          options={this.state.optionsOne}
          highcharts={Highcharts}
          constructorType={'stockChart'}
        />

        <HighchartsReact
          options={this.state.optionsTwo}
          highcharts={Highcharts}
          constructorType={'stockChart'}
        // allowChartUpdate={false}
        // immumate={true}
        />

        <HighchartsReact
          options={this.state.optionsThree}
          highcharts={Highcharts}
          constructorType={'stockChart'}
        // allowChartUpdate={false}
        // immumate={true}
        />
      </div>
    );
  }

}

export default App;
